.void-pass {
    padding: 100px 40px;
    width: 100%;
    height: 100%;
    min-height: 500px;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    background: #111111;
    flex-direction: column;
    align-items: center;
}

.section {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 40px;
    max-width: 1200px;
    width: 100%;
    /* margin-top: 70px; */
    /* background: red; */
}

