.void-pass li {
    font-size: 0.8rem;
    font-family: 'Inter', sans-serif;
    font-weight: 700;
    color: #fff;
    text-transform: uppercase;
    letter-spacing: 2px;
    /* line-height: 55px; */
    padding: 5px 0 5px 18px;
}

.right li:before {
    content: "";
    display: inline-block;
    margin: 13px 18px -5px -18px;
    width: 15px;
    height: 15px;
    background: url('https://res.cloudinary.com/mselbekk-com/image/upload/v1641893548/check_3_1_dorbkg.png');
    background-repeat: no-repeat;
}

