@import url(https://fonts.googleapis.com/css2?family=Inter:wght@500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}


html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}



/* @import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,600;0,700;0,900;1,600;1,900&display=swap'); */


.App {
  min-height: 100vh;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  color: #fff;
  background-color: black;
}

h1 {
  font-size: 2rem;
  font-family: 'Inter', sans-serif;
  font-weight: 900;
  color: #fff;
  text-transform: uppercase;
}

h2 {
  font-size: 1.2rem;
  font-family: 'Inter', sans-serif;
  font-weight: 800;
  color: #B92EE5;
  text-transform: uppercase;
  letter-spacing: 6px;
  margin-bottom: 2rem;
}

p {
  font-size: 0.8rem;
  font-family: 'Inter', sans-serif;
  font-weight: 700;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-bottom: 4rem;
  line-height: 25px;
}

li {
  font-size: 1rem;
  font-family: 'Inter', sans-serif;
  font-weight: 700;
  color: rgb(255, 255, 255);
  text-transform: uppercase;
  letter-spacing: 1px;
  list-style-type: none;
}

a {
  text-decoration: none;
  color: #fff;
}






.nav-container {
    padding: 0px 30px 0px;
    width: 100%;
    height: 100%;
    min-height: 100px;
    /* max-width: 1240px; */
    /* display: flex; */
    display: grid;
    grid-template-columns: 180px 1fr 180px;
    grid-auto-flow: column;
    align-items: center;
    background: transparent;
    position: relative;
    z-index: 999;

}

.logo {
    justify-content: start;
}

.logo img {
    width: 5.5rem;
}

.navigation {
    justify-content: center;
    display: flex;
    text-align: center;
}

.navigation ul {
    display: flex;
    align-items: center;
    list-style-type: none;
}

.navigation li {
    text-transform: uppercase;
    padding: 0px 15px;
}

.nav-container .wallet {
    justify-content: end;
    text-align: end;
}

.wallet button {
    font-size: 0.7rem;
    font-family: 'Inter', sans-serif;
    font-weight: 700;
    color: rgb(255, 255, 255);
    text-transform: uppercase;
    letter-spacing: 1px;
    padding: 20px 20px;
    background-color: #5E03C6;
    border-radius: 10px;
    border:none;
}

.wallet button img {
    width: 20px;
    margin-right: 10px;
}

.wallet .boom {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    color: #fff;
    /* position: relative; */
    justify-content: space-between;
    display: flex;
    align-items: center;
    /* margin: none; */
}

.wallet-button {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}



/*MEDIA QUIRIES*/

@media only screen and (max-width: 1100px) {
    .navigation li {
        font-size: 0.65rem;
    }

    .nav-container {
        grid-template-columns: auto 1fr auto;
    }
}

@media only screen and (max-width: 900px) {
    .navigation {
        display: none;
    }

    .nav-container {
        grid-template-columns: 1fr auto;
        padding: 0 20px;
    }

    .logo img {
        width: 4rem;
    }

    .wallet button {
        font-size: 0.7rem;
        font-family: 'Inter', sans-serif;
        font-weight: 700;
        color: rgb(255, 255, 255);
        text-transform: uppercase;
        letter-spacing: 1px;
        padding: 15px 25px;
        background-color: #5E03C6;
        border-radius: 10px;
        border:none;
    }
  }
