#void .timer {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    max-width: 600px;
    width: 100%;
    margin: auto;
    margin-top: 30px;
    grid-gap: 1rem;
}

#void .timer p {
  font-size: 1.5rem;
  font-family: 'Inter', sans-serif;
  font-weight: 900;
  text-transform: uppercase;
}

#void .timer p small {
  font-size: .8rem;
  font-family: 'Inter', sans-serif;
  font-weight: 600;
  text-transform: uppercase;
}

#void .space {
    background: linear-gradient(51.33deg, #5E03C6 39.03%, #3F33D5 100.2%);
    border-radius: 10px;
    padding: 2rem;
}

